import Link from 'next/link';

export default function Home() {
  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-gradient-to-br from-gray-900 via-black to-gray-800 text-white font-sans p-6">
      <main className="text-center max-w-4xl w-full">
        <div className="mb-12">
          <h1 className="text-6xl font-extrabold bg-clip-text text-transparent bg-gradient-to-r from-purple-400 to-pink-600 mb-6 tracking-tight">
            Next Generation App
          </h1>
          <p className="text-xl text-gray-400 max-w-2xl mx-auto leading-relaxed">
            Experience the future of web applications with our premium platform.
            Secure, fast, and beautifully designed.
          </p>
        </div>

        <div className="flex flex-col sm:flex-row items-center justify-center gap-6 mb-12">
          <Link href="/login"
            className="px-8 py-4 bg-white text-black font-bold rounded-full hover:bg-gray-200 transition-all duration-300 transform hover:scale-105 shadow-lg w-full sm:w-auto text-center"
          >
            Login
          </Link>

          <Link href="/signup"
            className="px-8 py-4 bg-transparent border-2 border-white/20 text-white font-bold rounded-full hover:bg-white/10 hover:border-white transition-all duration-300 transform hover:scale-105 shadow-lg w-full sm:w-auto text-center backdrop-blur-sm"
          >
            Sign Up
          </Link>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-8 text-left">
          {[
            { title: "Secure", desc: "Enterprise-grade security with Oracle Database integration." },
            { title: "Fast", desc: "Built on Next.js for blazing fast performance." },
            { title: "Modern", desc: "Designed with the latest aesthetic trends." }
          ].map((item, i) => (
            <div key={i} className="p-6 rounded-2xl bg-white/5 border border-white/10 hover:bg-white/10 transition-colors">
              <h3 className="text-xl font-bold mb-3 text-purple-300">{item.title}</h3>
              <p className="text-gray-400">{item.desc}</p>
            </div>
          ))}
        </div>
      </main>
    </div>
  );
}
